import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Box,
  TextField,
  InputAdornment,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Chip,
  Button,
  CardActions,
  Paper,
  Divider,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Search as SearchIcon,
  Favorite as FavoriteIcon,
  FavoriteBorder as FavoriteBorderIcon,
  FilterList as FilterIcon,
  Sort as SortIcon,
  ShoppingCart as CartIcon,
} from '@mui/icons-material';
import { useCart } from '../context/CartContext';

interface Product {
  id: string;
  title: string;
  description: string;
  price: number;
  condition: string;
  category: string;
  imageUrl: string;
  seller: {
    name: string;
    university: string;
    rating: number;
  };
  stock: number;
}

// Mock data
const mockProducts: Product[] = [
  {
    id: '1',
    title: 'Calculus Textbook - 10th Edition',
    description: 'Like new condition, barely used. All pages intact. Perfect for Calculus I and II courses.',
    price: 45.99,
    condition: 'Like New',
    category: 'Textbooks',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'John Doe',
      university: 'State University',
      rating: 4.5,
    },
    stock: 1,
  },
  {
    id: '2',
    title: 'TI-84 Plus Graphing Calculator',
    description: 'Essential calculator for math and science courses. Includes new batteries and manual.',
    price: 75.00,
    condition: 'Good',
    category: 'Electronics',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'Sarah Smith',
      university: 'Tech Institute',
      rating: 4.8,
    },
    stock: 2,
  },
  {
    id: '3',
    title: 'Chemistry Lab Kit',
    description: 'Complete lab kit for General Chemistry. Includes glassware, safety equipment, and storage case.',
    price: 120.00,
    condition: 'New',
    category: 'Lab Equipment',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'Mike Johnson',
      university: 'Science Academy',
      rating: 4.2,
    },
    stock: 3,
  },
  {
    id: '4',
    title: 'Physics Study Notes Bundle',
    description: 'Comprehensive study notes for Physics I & II. Includes practice problems and solutions.',
    price: 25.00,
    condition: 'Good',
    category: 'Notes',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'Emily Brown',
      university: 'State University',
      rating: 4.7,
    },
    stock: 5,
  },
  {
    id: '5',
    title: 'MacBook Pro 2021',
    description: 'M1 Pro chip, 16GB RAM, 512GB SSD. Perfect for computer science students.',
    price: 1299.99,
    condition: 'Like New',
    category: 'Electronics',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'Alex Chen',
      university: 'Tech Institute',
      rating: 4.9,
    },
    stock: 1,
  },
  {
    id: '6',
    title: 'Biology Lab Coat',
    description: 'White lab coat, size M. Required for biology and chemistry labs.',
    price: 29.99,
    condition: 'New',
    category: 'Lab Equipment',
    imageUrl: 'https://via.placeholder.com/200x300',
    seller: {
      name: 'Lisa Wang',
      university: 'Medical University',
      rating: 4.6,
    },
    stock: 4,
  },
];

const categories = [
  'All',
  'Textbooks',
  'Electronics',
  'Notes',
  'Lab Equipment',
  'Study Materials',
  'Other',
];

const conditions = ['All', 'New', 'Like New', 'Good', 'Fair'];

const sortOptions = [
  { value: 'price-asc', label: 'Price: Low to High' },
  { value: 'price-desc', label: 'Price: High to Low' },
  { value: 'rating-desc', label: 'Best Rated' },
  { value: 'newest', label: 'Newest First' },
];

const Products: React.FC = () => {
  const navigate = useNavigate();
  const { addItem } = useCart();
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [selectedCondition, setSelectedCondition] = useState('All');
  const [sortBy, setSortBy] = useState('newest');
  const [favorites, setFavorites] = useState<string[]>([]);

  const handleProductClick = (productId: string) => {
    navigate(`/products/${productId}`);
  };

  const toggleFavorite = (productId: string, event: React.MouseEvent) => {
    event.stopPropagation();
    setFavorites((prev) =>
      prev.includes(productId)
        ? prev.filter((id) => id !== productId)
        : [...prev, productId]
    );
  };

  const handleAddToCart = (product: Product, event: React.MouseEvent) => {
    event.stopPropagation();
    addItem({
      id: product.id,
      title: product.title,
      price: product.price,
      quantity: 1,
      imageUrl: product.imageUrl,
      seller: {
        name: product.seller.name,
        university: product.seller.university,
      },
    });
  };

  const filteredProducts = mockProducts
    .filter((product) => {
      const matchesSearch = product.title
        .toLowerCase()
        .includes(searchQuery.toLowerCase());
      const matchesCategory =
        selectedCategory === 'All' || product.category === selectedCategory;
      const matchesCondition =
        selectedCondition === 'All' || product.condition === selectedCondition;
      return matchesSearch && matchesCategory && matchesCondition;
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'price-asc':
          return a.price - b.price;
        case 'price-desc':
          return b.price - a.price;
        case 'rating-desc':
          return b.seller.rating - a.seller.rating;
        default:
          return 0;
      }
    });

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Typography variant="h4">Browse Products</Typography>
          <Box sx={{ display: 'flex', gap: 1 }}>
            <Tooltip title="Filter">
              <IconButton>
                <FilterIcon />
              </IconButton>
            </Tooltip>
            <Tooltip title="Sort">
              <IconButton>
                <SortIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Box>

        <Paper sx={{ p: 2, mb: 4 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                placeholder="Search products..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={2}>
              <FormControl fullWidth>
                <InputLabel>Category</InputLabel>
                <Select
                  value={selectedCategory}
                  label="Category"
                  onChange={(e) => setSelectedCategory(e.target.value)}
                >
                  {categories.map((category) => (
                    <MenuItem key={category} value={category}>
                      {category}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={2}>
              <FormControl fullWidth>
                <InputLabel>Condition</InputLabel>
                <Select
                  value={selectedCondition}
                  label="Condition"
                  onChange={(e) => setSelectedCondition(e.target.value)}
                >
                  {conditions.map((condition) => (
                    <MenuItem key={condition} value={condition}>
                      {condition}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={2}>
              <FormControl fullWidth>
                <InputLabel>Sort By</InputLabel>
                <Select
                  value={sortBy}
                  label="Sort By"
                  onChange={(e) => setSortBy(e.target.value)}
                >
                  {sortOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        </Paper>

        <Box sx={{ mb: 2 }}>
          <Typography variant="subtitle1" color="text.secondary">
            {filteredProducts.length} products found
          </Typography>
        </Box>

        <Grid container spacing={3}>
          {filteredProducts.map((product) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={product.id}>
              <Card
                sx={{
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  cursor: 'pointer',
                  position: 'relative',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: 4,
                    transition: 'all 0.3s ease',
                  },
                }}
                onClick={() => handleProductClick(product.id)}
              >
                <CardMedia
                  component="img"
                  height="200"
                  image={product.imageUrl}
                  alt={product.title}
                  sx={{ objectFit: 'cover' }}
                />
                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography variant="h6" gutterBottom noWrap>
                    {product.title}
                  </Typography>
                  <Typography
                    variant="body2"
                    color="text.secondary"
                    sx={{
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      display: '-webkit-box',
                      WebkitLineClamp: 2,
                      WebkitBoxOrient: 'vertical',
                      mb: 2,
                      height: '40px',
                    }}
                  >
                    {product.description}
                  </Typography>
                  <Box sx={{ mt: 'auto' }}>
                    <Typography variant="h6" color="primary" gutterBottom>
                      ${product.price.toFixed(2)}
                    </Typography>
                    <Box sx={{ display: 'flex', gap: 0.5, flexWrap: 'wrap' }}>
                      <Chip
                        label={product.condition}
                        size="small"
                        color="primary"
                        variant="outlined"
                      />
                      <Chip
                        label={product.category}
                        size="small"
                        color="secondary"
                        variant="outlined"
                      />
                      {product.stock <= 3 && (
                        <Chip
                          label={`Only ${product.stock} left`}
                          size="small"
                          color="error"
                          variant="outlined"
                        />
                      )}
                    </Box>
                  </Box>
                </CardContent>
                <Divider />
                <CardActions sx={{ justifyContent: 'space-between', p: 2 }}>
                  <Box>
                    <Typography variant="caption" color="text.secondary" display="block">
                      {product.seller.university}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      Rating: {product.seller.rating}/5
                    </Typography>
                  </Box>
                  <Box sx={{ display: 'flex', gap: 1 }}>
                    <Tooltip title={favorites.includes(product.id) ? 'Remove from favorites' : 'Add to favorites'}>
                      <IconButton
                        size="small"
                        onClick={(e) => toggleFavorite(product.id, e)}
                        color={favorites.includes(product.id) ? 'error' : 'default'}
                      >
                        {favorites.includes(product.id) ? (
                          <FavoriteIcon />
                        ) : (
                          <FavoriteBorderIcon />
                        )}
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="Add to cart">
                      <IconButton
                        size="small"
                        color="primary"
                        onClick={(e) => handleAddToCart(product, e)}
                      >
                        <CartIcon />
                      </IconButton>
                    </Tooltip>
                  </Box>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>

        {filteredProducts.length === 0 && (
          <Paper sx={{ p: 4, textAlign: 'center' }}>
            <Typography variant="h6" color="text.secondary" gutterBottom>
              No products found
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Try adjusting your search criteria or browse all products
            </Typography>
            <Button
              variant="contained"
              color="primary"
              sx={{ mt: 2 }}
              onClick={() => {
                setSearchQuery('');
                setSelectedCategory('All');
                setSelectedCondition('All');
              }}
            >
              Clear Filters
            </Button>
          </Paper>
        )}
      </Box>
    </Container>
  );
};

export default Products; 